.backdrop {
  width: 100vw;
  position: absolute;
  z-index: 10;
  background: linear-gradient(-47deg, #4935ff, #4f2ee8, #4b34ae, #833ab4, #6417bd) 0 0/400%
    400%;
  transition: opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1),
    visibility 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  animation: 8s infinite gradient;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  overflow-y: auto;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@media (min-width: 1185px) {
  .backdrop {
    height: 100vh;
  }
}
