.title {
  font-size: 32px;
  color: #f6f6f6;
  margin-top: 20px;
  margin-bottom: 20px;
}

.label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.label_text {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  font-weight: 500;
  color: #f6f6f6;
  font-family: 'DM Sans';
}

.input {
  outline: none;
  padding: 20px 30px;
  transition: all ease-in-out 500ms;
  border-radius: 40px;
  border: 2px solid #f6f6f6;
  background-color: transparent;
  color: #f6f6f6;
  font-family: 'DM Sans';
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.18px;
  text-transform: uppercase;
}
.input:hover {
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.2);
  border-color: #eac645;
}
.input:focus {
  border-color: #eac645;
}
